import React from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import {
  Dropdown,
  Icon,
  Input,
  Menu,
  Label,
  Button,
  Segment,
  Rail,
  Message,
  Divider,
  List
} from "semantic-ui-react";

import { getStorage } from "../Storage";
import "./Menu.css";

export default class SideMenu extends React.Component {
  static propTypes = {
    firebase: PropTypes.object.isRequired
  };

  constructor(props) {
    super(props);

    this.state = {
      isHidden: false,
      search: "",
      tag: null,
      expanded: []
    };
  }

  // ===========================================================================
  // Handlers

  handleToggleExpanded = (e, name) => {
    const { expanded } = this.state;
    this.setState({
      expanded: expanded.includes(name)
        ? expanded.filter(item => item !== name)
        : [...expanded, name]
    });
  };

  handleToggleHidden = () => {
    this.setState({ isHidden: !this.state.isHidden });
  };

  handleSearch = e => {
    this.setState({ search: e.target.value });
  };

  handleClearSearch = () => {
    this.setState({ search: "" });
  };

  handleToggleTag = tag => {
    this.setState({ tag: this.state.tag === tag ? null : tag });
  };

  handleLogOut = () =>
    this.props.firebase
      .auth()
      .signOut()
      .then(() => window.location.reload());

  handleDelete = () =>
    this.props.firebase
      .auth()
      .currentUser.delete()
      .then(() => window.location.reload())
      .catch(e => {
        // TODO:
        alert("nooo");
      });

  // ===========================================================================
  // Hooks

  componentDidMount() {
    this.unsubscribe = getStorage().subscribe(() => this.forceUpdate());
  }

  componentWillUnmount() {
    this.unsubscribe();
  }

  // ===========================================================================
  // Rendering

  renderUserInfo() {
    const { currentUser } = this.props.firebase.auth();
    return (
      <Menu compact icon size="tiny">
        <Dropdown item icon="user">
          <Dropdown.Menu>
            <Menu.Item disabled>
              <Label>
                <Icon name="user" /> {currentUser.displayName}
              </Label>
              <br /> <br />
              <Label>
                <Icon name="mail" /> {currentUser.email}
              </Label>
            </Menu.Item>
            <Divider />
            <Dropdown.Item
              icon="log out"
              text="Logout"
              onClick={this.handleLogOut}
            />
            <Dropdown.Item
              icon="delete"
              text="Delete"
              onClick={this.handleDelete}
            />
            <Dropdown.Item
              icon="edit"
              text="Edit"
              onClick={() => {
                // TODO:
                alert("TODO:");
              }}
            />
          </Dropdown.Menu>
        </Dropdown>
      </Menu>
    );
  }

  renderTags() {
    const store = getStorage();
    return (
      <Menu.Menu>
        {Object.entries(store.getNotesByTagsAndDate()).map(([tag, notes]) => (
          <Menu.Item
            key={tag}
            active={this.state.tag === tag}
            onClick={() => this.handleToggleTag(tag)}
          >
            #{tag}
            <Label {...(this.state.tag === tag ? { color: "teal" } : {})}>
              {notes.length}
            </Label>
          </Menu.Item>
        ))}
      </Menu.Menu>
    );
  }

  render() {
    const { isHidden, search, tag, expanded } = this.state;
    const store = getStorage();
    const selected = store.getSelectedNote();

    const allActiveNotes = store.getActiveNotes();
    const inactiveNotes = store.getInactiveNotes();

    let activeNotes = allActiveNotes;
    if (search) {
      const filterFn = n =>
        n.meta.title.toLowerCase().includes(search.toLowerCase());
      activeNotes = activeNotes.filter(filterFn);
    }

    if (tag) {
      const filterFn = n => n.meta.tags.includes(tag);
      activeNotes = activeNotes.filter(filterFn);
    }

    if (isHidden)
      return (
        <Menu className="Menu--Trigger" compact icon size="tiny">
          <Menu.Item name="caret square left" onClick={this.handleToggleHidden}>
            <Icon name="caret square right" />
          </Menu.Item>
        </Menu>
      );

    return (
      <section className="Menu">
        {this.renderUserInfo()}
        <Menu compact icon size="tiny" floated="right">
          <Menu.Item link onClick={() => store.addNewNote()}>
            <Icon name="plus" color="olive" />
          </Menu.Item>
        </Menu>
        <div style={{ clear: "both" }}></div>
        <Menu vertical>
          <Menu.Item>
            <Input
              placeholder="Search..."
              value={search}
              onChange={this.handleSearch}
              {...(!!search.trim()
                ? {
                    icon: {
                      name: "close",
                      link: true,
                      onClick: this.handleClearSearch
                    }
                  }
                : {})}
            />
          </Menu.Item>

          <Menu.Item className="Menu--Notes">
            Notes
            {activeNotes.length !== allActiveNotes.length && (
              <Label>
                {activeNotes.length} / {allActiveNotes.length}
              </Label>
            )}
            <Divider />
            <Menu.Menu>
              {activeNotes.length === 0 && (
                <Message visible className="Menu--Notes--Message">
                  No notes found.
                </Message>
              )}

              {activeNotes.map(n => (
                <Dropdown
                  item
                  key={n.id}
                  text={n.meta.title || "--"}
                  direction="left"
                  className={classNames(n === selected && "active")}
                  onClick={e => {
                    e.stopPropagation();
                    store.selectNote(n.id);
                  }}
                  {...(n !== selected ? { open: false } : {})}
                >
                  <Dropdown.Menu>
                    <Dropdown.Item
                      icon="archive"
                      text="Archive"
                      onClick={e => {
                        e.stopPropagation();
                        store.deleteNote(n.id);
                      }}
                    />
                  </Dropdown.Menu>
                </Dropdown>
              ))}
            </Menu.Menu>
          </Menu.Item>

          <Menu.Item
            className={classNames(
              "Menu--Archive",
              expanded.includes("archive") && "Menu--Archive-active"
            )}
            onClick={e => this.handleToggleExpanded(e, "archive")}
            active={expanded.includes("archive")}
          >
            Archive
            <Icon name="trash" />
            {expanded.includes("archive") && (
              <>
                <Menu.Menu>
                  {inactiveNotes.map(n => (
                    <Dropdown
                      key={n.id}
                      item
                      text={n.meta.title || "--"}
                      direction="left"
                      className={classNames(n === selected && "active")}
                    >
                      <Dropdown.Menu>
                        <Dropdown.Item
                          icon="edit"
                          text="Open"
                          onClick={() => store.selectNote(n.id)}
                        />
                        <Dropdown.Item
                          icon="undo"
                          text="Restore"
                          onClick={e => {
                            e.stopPropagation();
                            store.restoreNote(n.id);
                          }}
                        />
                        <Dropdown.Item
                          icon="delete"
                          text="Delete"
                          onClick={e => {
                            e.stopPropagation();
                            store.permanentlyDeleteNote(n.id);
                          }}
                        />
                      </Dropdown.Menu>
                    </Dropdown>
                  ))}
                </Menu.Menu>
              </>
            )}
          </Menu.Item>

          <Menu.Item>{this.renderTags()}</Menu.Item>
        </Menu>
        <Menu compact icon size="tiny" floated="right">
          <Menu.Item name="caret square left" onClick={this.handleToggleHidden}>
            <Icon name="caret square left" />
          </Menu.Item>
        </Menu>
      </section>
    );
  }
}
